<template>
  <div>
    <h1 class="ap-panel-heading__1">Message 消息提示</h1>
    <p>全局方法</p>
    <ap-slide>
      <template slot="component">
        <p @click="open">打开消息提示</p>
      </template>
      <template slot="code">
        &lt;p @click="open">打开消息提示&lt;/>
        <pre>
          this.$apMessage({
            content: '这是一条提示消息'
          })
        </pre>
      </template>
    </ap-slide>
  </div>
</template>

<script>
import apSlide from '@/components/Basic/Slide'
  export default {
    components: {
      apSlide
    },
    methods: {
      open() {
        this.$apMessage({
          content: '这是一条提示消息'
        })
      }
     }
  }
</script>

